						// 如果L数据为空
						if(minlength == 0){
							
						}else{
							for (var i = 0; i < minlength; i++) {
								let arr = []
								arr.push(RdatatArray[i][0])
								arr.push(RdatatArray[i][1])
								arr.push(LdataArray[i][0])
								arr.push(LdataArray[i][1])
